<template>
  <div :id="id" class="ring" style="width: 100%; height: 100%;"></div>
</template>
<script>
export default {
  props: {
    id: {
      default: function () {
        return "";
      }
    },
    echartData: {
      type: Array,
      default: function () {
        return [];
      }
    }
  },
  data() {
    return {
      echartsOption: null,
      screenWidth: document.body.clientWidth
    };
  },
  computed: {},
  watch: {
    echartData: {
      deep: true,
      handler: function () {
        this.setChart();
      }
    },
    screenWidth(val) {
      this.screenWidth = val;
      this.echartsOption.resize()
    }
  },
  activated() { },
  mounted() {
    const self = this;
    // 基于准备好的dom，初始化echarts实例
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        self.screenWidth = window.screenWidth
      })()
    };
    this.setChart();
  },
  methods: {
    setChart() {
      //当前视口宽度
      let nowClientWidth = document.documentElement.clientWidth;
      // 换算方法
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };
      let data = this.echartData;
      let arrName = getArrayValue(data, "name");
      let arrValue = getArrayValue(data, "value");
      let sumValue = eval(arrValue.join("+"));
      let objData = array2obj(data, "name");
      let optionData = getData(data);
      function getArrayValue(array, key) {
        var key = key || "value";
        var res = [];
        if (array) {
          array.forEach(function (t) {
            res.push(t[key]);
          });
        }
        return res;
      }

      function array2obj(array, key) {
        var resObj = {};
        for (var i = 0; i < array.length; i++) {
          resObj[array[i][key]] = array[i];
        }
        return resObj;
      }

      // 计算总值
      function gross(data) {
        let grossNum = 0
        for (const i in data) {
          let vm = data[i]
          grossNum += vm.value
        }
        return grossNum
      }
      let groNum = gross(data)

      function getData(data) {
        let colors = ["#32F1FE", "#DBBC0B", "#2162FF", "#FF5D5D", "#40C15F"];
        var res = {
          series: [],
          yAxis: []
        };
        for (let i = 0; i < data.length; i++) {
          res.series.push({
            name: "",
            type: "pie",
            clockWise: false, //顺时加载
            hoverAnimation: false, //鼠标移入变大
            radius: [70 - i * 15 + "%", 68 - i * 15 + "%"],
            center: ["50%", "43%"],
            label: {
              show: false
            },
            itemStyle: {
              label: {
                show: false
              },
              labelLine: {
                show: false
              },
              normal: {
                borderWidth: nowSize(6),
                borderColor: colors[i],
                shadowColor: colors[i]
              }
            },
            data: [
              {
                value: data[i].value,
                name: data[i].name
              },
              {
                value: sumValue - data[i].value,
                name: "",
                itemStyle: {
                  color: "rgba(0,0,0,0)",
                  borderWidth: 0
                },
                tooltip: {
                  show: false
                },
                hoverAnimation: false
              }
            ]
          });
          res.series.push({
            name: "",
            type: "pie",
            silent: true,
            z: 1,
            clockWise: false, //顺时加载
            hoverAnimation: false, //鼠标移入变大
            radius: [73 - i * 15 + "%", 68 - i * 15 + "%"],
            center: ["50%", "44%"],
            label: {
              show: false
            },
            itemStyle: {
              label: {
                show: false
              },
              labelLine: {
                show: false
              },
              borderWidth: nowSize(5)
            },
            data: [
              {
                value: 7.5,
                itemStyle: {
                  color: "rgba(26, 61, 157, 0.6)",
                  borderWidth: nowSize(5)
                },
                tooltip: {
                  show: false
                },
                hoverAnimation: false
              },
              {
                value: 2.5,
                name: "",
                itemStyle: {
                  color: "rgba(0,0,0,0)",
                  borderWidth: 0
                },

                tooltip: {
                  show: false
                },
                hoverAnimation: false
              }
            ]
          });
          res.yAxis.push(((data[i].value / sumValue) * 100).toFixed(2) + "%");
        }
        return res;
      }
      // let vm = this;
      let infoData = this.echartData;
      this.echartsOption = this.$echarts.init(
        document.getElementById(this.id)
      );
      this.echartsOption.clear();
      this.echartsOption.setOption({
        // backgroundColor: '#000',
        legend: {
          show: true,
          icon: "none",
          top: "center",
          top: "6%",
          left: "44%",
          data: arrName,
          width: nowSize(30),
          padding: [0, 0, 0, nowSize(40)],
          itemGap: nowSize(14),
          formatter: function (name) {
            return (
              "{title|" +
              name +
              "}{value|(" +
              ((objData[name].value/groNum) * 100).toFixed(0) + "%" +
              ")}"
            );
          },
          textStyle: {
            rich: {
              title: {
                fontSize: nowSize(14),
                color: function (params) {
                  return colors[params.dataIndex];
                }
              },
              value: {
                fontSize: nowSize(14),
                color: function (params) {
                  return colors[params.dataIndex];
                }
              }
            }
          }
        },
        tooltip: {
          show: true,
          trigger: "item",
          formatter: "{b}:{c}({d}%)"
        },
        color: ["#32F1FE", "#DBBC0B", "#2162FF", "#FF5D5D", "#40C15F"],
        xAxis: [
          {
            show: false
          }
        ],
        series: optionData.series
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
