<template>
  <div :id="id" class="pieFwXs" style="width: 100%; height: 100%"></div>
</template>
<script>
export default {
  props: {
    id: {
      // type: Number,
      default: function () {
        return "21321";
      },
    },
    echartData: {
      type: Array,
      default: function () {
        return [
          //   {
          //     name: "面谈",
          //     value: 30,
          //   },
          //   {
          //     name: "电话",
          //     value: 10,
          //   },
          //   {
          //     name: "网络",
          //     value: 30,
          //   },
        ];
      },
    },
  },
  data: function () {
    return {
      echartsOption: null,
      screenWidth: document.body.clientWidth,
    };
  },
  computed: {},
  watch: {
    echartData: {
      // deep: true,
      handler: function () {
        this.setChart();
      },
    },
    screenWidth(val) {
      this.screenWidth = val;
      this.echartsOption.resize();
    },
  },
  activated() {},
  mounted() {
    const self = this;
    // 基于准备好的dom，初始化echarts实例
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth;
        self.screenWidth = window.screenWidth;
      })();
    };
    this.setChart();
  },
  methods: {
    /**
     * @introduction 把数组中key值相同的那一项提取出来，组成一个对象
     * @description 详细描述
     * @param {参数类型} array 传入的数组 [{a:"1",b:"2"},{a:"2",b:"3"}]
     * @param {参数类型} key  属性名 a
     * @return {返回类型说明}
     * @exception [违例类型] [违例类型说明]
     */
    array2obj(array, key) {
      var resObj = {};
      for (var i = 0; i < array.length; i++) {
        resObj[array[i][key]] = array[i];
      }
      return resObj;
    },

    setChart() {
      this.setRoseChart();
    },
    setRoseChart() {
      //当前视口宽度
      let nowClientWidth = document.documentElement.clientWidth;
      // 换算方法
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };
      let echartData = this.echartData;
      //   要展示的环的颜色
      let colors = [
        "#61E57F",
        "#34FFFD",
        "#2467FF",
        "#6e7ce0",
        "#5fede1",
        "#388df6",
        "#7fdefe",
      ];
      let data = [];
      let legendData = [];
      for (var i = 0; i < echartData.length; i++) {
        data.push({
          value: echartData[i].value,
          name: echartData[i].name,
          label: {
            color: colors[i],
          },
          // itemStyle: {
          //   normal: {
          //     borderWidth: 0,
          //     shadowBlur: 10,
          //     borderColor: colors[i],
          //     shadowColor: colors[i],
          //   },
          // },
        });
        // 右边图例部分的数据
        legendData.push({
          name: echartData[i].name,
          icon: "circle",
        });
      }
      let objData = this.array2obj(echartData, "name");
      this.echartsOption = this.$echarts.init(document.getElementById(this.id));
      this.echartsOption.clear();
      this.echartsOption.setOption({
        color: colors,
        grid: {
          left: "5px", // 与容器左侧的距离
          right: "5px", // 与容器右侧的距离
          top: "0%", // 与容器顶部的距离
          bottom: "5px", // 与容器底部的距离
        },
        legend: {
          orient: "vertical",
          top: "center",
          left: "60%",
          itemGap: nowSize(20),
          data: legendData,
          formatter: function (name) {
            // console.log("name:", name, objData);
            return `{title|${name}:} {value|${objData[name].value}} {unit|(万元)}`;
          },
          textStyle: {
            rich: {
              title: {
                color: "#ffffff",
                fontSize: nowSize(16),
                fontWeight: 600,
                fontFamily: "HuaKang",
                padding: [0, 10, 0, 10],
              },
              value: {
                color: "#ffffff",
                fontSize: nowSize(23),
                fontWeight: 600,
                fontFamily: "HuaKang",
                padding: [0, 0, 0, 10],
              },
              unit: {
                color: "#ffffff",
                fontSize: nowSize(13),
                fontWeight: 600,
                fontFamily: "HuaKang",
                padding: [0, 0, 0, 0],
              },
            },
          },
        },
        tooltip: {
          trigger: "item",
          //   formatter: "{a} <br/>{b} : {c} ({d}%)",
          formatter: function (param) {
            return param.name + ": " + param.value.toFixed(2) + " ";
          },
        },
        series: [
          {
            type: "gauge",
            name: "外层辅助",
            radius: "38%",
            center: ["28%", "43%"],
            startAngle: "225",
            endAngle: "-134.999",
            splitNumber: "80",
            pointer: {
              show: false,
            },
            detail: {
              show: false,
            },
            data: [
              {
                value: 1,
              },
            ],
            // data: [{value: 1, name: 90}],

            axisLine: {
              show: true,
              lineStyle: {
                color: [[1, "#00FFFF"]],
                width: nowSize(20),
                opacity: 0.1,
              },
            },
            axisTick: {
              show: false,
            },
            splitLine: {
              show: true,
              length: nowSize(20),
              lineStyle: {
                color: "#051932",
                width: 0,
                type: "solid",
              },
            },
            axisLabel: {
              show: false,
            },
          },
          {
            type: "gauge",
            name: "外层辅助",
            radius: "25%",
            center: ["28%", "43%"],
            startAngle: "-30",
            endAngle: "-140",
            splitNumber: "80",
            pointer: {
              show: false,
            },
            detail: {
              show: false,
            },
            data: [
              {
                value: 1,
              },
            ],
            // data: [{value: 1, name: 90}],

            axisLine: {
              show: true,
              lineStyle: {
                color: [[1, "#00FFFF"]],
                width: nowSize(4),
                opacity: 0.1,
              },
            },
            axisTick: {
              show: false,
            },
            splitLine: {
              show: true,
              length: nowSize(20),
              lineStyle: {
                color: "#051932",
                width: 0,
                type: "solid",
              },
            },
            axisLabel: {
              show: false,
            },
          },
          {
            type: "gauge",
            name: "外层辅助",
            radius: "25%",
            center: ["28%", "43%"],
            startAngle: "-150",
            endAngle: "-260",
            splitNumber: "80",
            pointer: {
              show: false,
            },
            detail: {
              show: false,
            },
            data: [
              {
                value: 1,
              },
            ],
            // data: [{value: 1, name: 90}],

            axisLine: {
              show: true,
              lineStyle: {
                color: [[1, "#00FFFF"]],
                width: nowSize(4),
                opacity: 0.1,
              },
            },
            axisTick: {
              show: false,
            },
            splitLine: {
              show: true,
              length: nowSize(20),
              lineStyle: {
                color: "#051932",
                width: 0,
                type: "solid",
              },
            },
            axisLabel: {
              show: false,
            },
          },
          {
            type: "gauge",
            name: "外层辅助",
            radius: "25%",
            center: ["28%", "43%"],
            startAngle: "-270",
            endAngle: "-380",
            splitNumber: "80",
            pointer: {
              show: false,
            },
            detail: {
              show: false,
            },
            data: [
              {
                value: 1,
              },
            ],
            // data: [{value: 1, name: 90}],

            axisLine: {
              show: true,
              lineStyle: {
                color: [[1, "#00FFFF"]],
                width: nowSize(4),
                opacity: 0.1,
              },
            },
            axisTick: {
              show: false,
            },
            splitLine: {
              show: true,
              length: nowSize(20),
              lineStyle: {
                color: "#051932",
                width: 0,
                type: "solid",
              },
            },
            axisLabel: {
              show: false,
            },
          },
          {
            name: "",
            type: "pie",
            clockWise: false,
            startAngle: 90,
            radius: ["45%", "58%"],
            center: ["28%", "43%"],
            hoverAnimation: true,
            data: data,
            label: {
              fontSize: nowSize(18),
              // alignTo: 'labelLine', // 'edge', 'labelLine', 默认是'none'
              formatter: function (params) {
                let percent = 0;
                let total = 0;
                for (var i = 0; i < data.length; i++) {
                  total += data[i].value;
                }
                percent = ((params.value / total) * 100).toFixed(0);
                return params.name + "\n" + "(" + percent + "%" + ")";
              },
            },
            labelLine: {
              length: nowSize(30),
              length2: nowSize(20),
              lineStyle: {
                width: nowSize(2),
              },
            },
            hoverAnimation: true,
          },
          {
            name: "",
            type: "pie",
            radius: ["64%", "66%"],
            center: ["28%", "43%"],
            data: "100%",
            itemStyle: {
              color: "#10336E",
              opacity: 0.6,
            },
            label: {
              show: false,
            },
          },
        ],
      });
    },
  },
};
</script>
