<script>
import Vue from "vue";
import { mapGetters } from "vuex";
import AMapLoader from "@/utils/aMap.js";
export default {
  name: "aMap",
  components: {},
  watch: {},
  computed: {},
  data() {
    return {
      circleTip: [
        { title: "志愿者", type: "0", image: "circle1" },
        { title: "工单", type: "1", image: "circle2" },
      ],
      markerStyles: null,
      massMarks: null,
      tableData: [],
      clickItem: [],
      markDatas: null,
      infoWindow: null,
      map: null,
    };
  },
  computed: {
    // getNumberTip() {
    //   return "人";
    // }
  },
  methods: {
    // showAreaData(id) {},
    /*
    22.568283,113.895858 新安街道
    22.580897,113.883902 西乡街道
    22.628623,113.841914 航城街道
    22.671404,113.822755 福永街道
    22.688173,113.794997 福海街道
    22.733314,113.811647 沙井街道
    22.743085,113.829852 新桥街道
    22.764615,113.848396 松岗街道
    22.798532,113.866233 燕罗街道
    22.681693,113.939388 石岩街道*/
    showData() {
      var data = [
        { latitude: 22.568283, longitude: 113.895858, areaName: "新安街道" ,companyNum:12,fee:254},
        { latitude: 22.580897, longitude: 113.883902, areaName: "西乡街道" ,companyNum:8,fee:124},
        { latitude: 22.628623, longitude: 113.841914, areaName: "航城街道" ,companyNum:9,fee:334},
        { latitude: 22.671404, longitude: 113.822755, areaName: "福永街道" ,companyNum:12,fee:124},
        { latitude: 22.688173, longitude: 113.794997, areaName: "福海街道" ,companyNum:11,fee:94},
        { latitude: 22.733314, longitude: 113.811647, areaName: "沙井街道" ,companyNum:13,fee:234},
        { latitude: 22.743085, longitude: 113.829852, areaName: "新桥街道" ,companyNum:11,fee:141},
        { latitude: 22.764615, longitude: 113.848396, areaName: "松岗街道" ,companyNum:14,fee:222},
        { latitude: 22.798532, longitude: 113.866233, areaName: "燕罗街道" ,companyNum:6,fee:101},
        { latitude: 22.681693, longitude: 113.939388, areaName: "石岩街道" ,companyNum:9,fee:99},
      ];
      var tData = [];
      let that = this;
      var markerData = [];
      var styleIndex = 0;
      this.markDatas = data;
    
      data.forEach((element) => {
        tData.push({
          name: element.areaName,
          count: 100,
        });

        if (
          element.longitude != undefined &&
          element.latitude != undefined &&
          element.longitude != null &&
          element.latitude != null
        ) {
          var lat = parseFloat(element.latitude);
          var lon = parseFloat(element.longitude);

          console.log(`${lat},${lon}`);

          markerData.push({
            lnglat: [lon, lat],
            id: data.indexOf(element),
            style: styleIndex, // 该数据的样式取值styleObjectArr对应的样式索引
          });
        }
      });
      this.tableData = tData;
      if (this.massMarks != null) {
        this.massMarks.clear();
      }
      this.massMarks = new AMap.MassMarks(markerData, {
        zIndex: 111,
        style: this.markerStyles,
      });
      this.massMarks.on("click", (e) => {
        console.log(e);
        var element = this.markDatas[e.data.id];
        this.clickItem = [
          {
            name:"地区",
            value:element.areaName
          },
          {
            name:"企业数量",
            value:element.companyNum+"家"
          },
          {
            name:"总支出",
            value:element.fee+"万"
          }
        ];
        console.log(this.clickItem);
        //this.$refs.pop

        this.infoWindow = new AMap.InfoWindow({
          isCustom: true, //使用自定义窗体
          content: this.$refs.pop, //传入 dom 对象，或者 html 字符串
        });
        this.infoWindow.open(this.map, e.data.lnglat);
      });
      // 将 massMarks 添加到地图实例
      this.massMarks.setMap(this.map);
    },
  },
  mounted() {
    let that = this;

    console.log("preInitMap");
    // this.getMapData(this.circleTip[0].type)
    Promise.all([AMapLoader()]).then((values) => {
      console.log("values:", values);
      this.map = new AMap.Map("map", {
        zoom: 11, //设置地图的缩放级别
        mapStyle: "amap://styles/ec036383cbc0a7d1c55b52d242a1d57c",
        center: [113.8821168109775, 22.597056195463144],
      });

      this.markerStyles = [
        {
          url: "https://a.amap.com/jsapi_demos/static/images/mass0.png", // 图标地址
          size: new AMap.Size(11, 11), // 图标大小
          anchor: new AMap.Pixel(5, 5), // 图标显示位置偏移量，基准点为图标左上角
        },
        {
          url: "https://a.amap.com/jsapi_demos/static/images/mass1.png", // 图标地址
          size: new AMap.Size(11, 11), // 图标大小
          anchor: new AMap.Pixel(5, 5), // 图标显示位置偏移量，基准点为图标左上角
        },
      ];
      // if (values.length == 2 && values[1].code == 200) {
        console.log("success");
        this.showData();
      // }

      // this.massMarks = new AMap.MassMarks({
      //   zIndex: 5, // 海量点图层叠加的顺序
      //   zooms: [3, 19], // 在指定地图缩放级别范围内展示海量点图层
      //   style: this.markerStyles //多种样式对象的数组
      // });
    });
  },
};
</script>
