<template>
  <div class="chart-rose">
    <div :id="id" class="pie" style="width: 100%; height: 100%"></div>
  </div>
</template>
<script>
export default {
  name: "chartRose",
  props: {
    id: {
      // 图唯一标识
      default: function () {
        return "";
      },
    },
    fontSize: {
      type: Number,
      default: 15,
    },
    xAxis: {
      // x轴数据
      type: Array,
      default: function () {
        return [];
        // return [];
      },
    },
    yAxis: {
      // y轴数据
      type: Array,
      default: function () {
        return [];
      },
    },
  },
  data: function () {
    return {
      echartsOption: null,
      screenWidth: document.body.clientWidth,
    };
  },
  computed: {},
  watch: {
    yAxis: {
      deep: true,
      handler: function () {
        this.initChart();
      },
    },
    screenWidth(val) {
      this.screenWidth = val;
      this.echartsOption.resize();
    },
  },
  activated() {},
  mounted() {
    const self = this;
    // 基于准备好的dom，初始化echarts实例
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth;
        self.screenWidth = window.screenWidth;
      })();
    };
    this.initChart();
  },
  methods: {
    initChart() {
      //当前视口宽度
      let nowClientWidth = document.documentElement.clientWidth;
      // 换算方法
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };
      this.echartsOption = this.$echarts.init(document.getElementById(this.id));
      this.echartsOption.clear();
      let lableV = {
        normal: {
          formatter: "{b}\n{c}亿元",
          padding: 4,
          color: "#fff",
          fontSize: nowSize(14),
          fontFamily: "Microsoft YaHei",
          fontWeight: 700,
        },
      };

      let echartData = [];
      for (const i in this.xAxis) {
        echartData.push({
          name: this.xAxis[i],
          value: this.yAxis[i] || 0,
          label: lableV,
        });
      }
      let option = {
        series: [
          {
            type: "pie",
            clockWise: false,
            radius: ["20%", "70%"],
            center: ["50%", "50%"],
            startAngle: 90,
            roseType: "area",
            itemStyle: {
              normal: {
                color: function (params) {
                  var colorList = [
                    "#2E46FF",
                    "#537BFF",
                    "#52BCFF",
                    "#00FFFF",
                    "#20D666",
                    "#70F785",
                    "#FF8D45",
                    "#FFF145",
                    "#FF5858",
                  ];
                  return colorList[params.dataIndex];
                },
              },
            },
            data: echartData,
          },
        ],
      };
      this.echartsOption.setOption(option);
    },
  },
};
</script>
<style lang="scss">
.chart-rose {
  position: relative;
  width: 100%;
  height: 100%;
  .pie {
    width: 100%;
    height: 100%;
  }
}
</style>
