<template>
  <div :id="id" class="pie" style="width: 100%; height: 100%"></div>
</template>
<script>
export default {
  props: {
    id: {
      default: function () {
        return "";
      },
    },
    echartData: {
      type: Array,
      default: function () {
        return [];
      },
    },
    leftTitleData: {
      type: Array,
      default: function () {
        return [];
      },
    },
    unit: {
      type: String,
      default: function () {
        return '项';
      },
    },
  },
  data: function () {
    return {
      screenWidth: document.body.clientWidth,
      echartsOption: "",
    };
  },
  computed: {},
  watch: {
    echartData: {
      deep: true,
      handler: function () {
        this.setChart();
      },
    },
    screenWidth(val) {
      this.screenWidth = val;
      this.echartsOption.resize();
    },
  },
  activated() {},
  mounted() {
    const self = this;
    // 基于准备好的dom，初始化echarts实例
    (window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth;
        self.screenWidth = window.screenWidth;
      })();
    }),
      this.setChart();
  },
  methods: {
    setChart() {
      let nba = this.unit
      //当前视口宽度
      let nowClientWidth = document.documentElement.clientWidth;
      // 换算方法
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };
      let infoData = this.echartData;
      // 遍历函数
      let ergodic = function (list) {
        let target = []
        for (const i in list) {
          target.push(
            nowSize(list[i])
          )
        }
        return target
      }
      // y x 字的 padding
      let paddingY = [0, 0, 35, 0]
      let paddingX = [0, 10, 35, 0]
      paddingY = ergodic(paddingY)
      paddingX = ergodic(paddingX)
      console.log("处理后的paddingY", paddingY)
      // 线宽
      let barWidth = nowSize(10);
      // 字体大小，左右
      let fontSize = nowSize(16);
      this.echartsOption = this.$echarts.init(document.getElementById(this.id));
      this.echartsOption.clear();
      this.echartsOption.setOption({
        grid: {
          left: "8%",
          right: "0%",
          bottom: "5%",
          top: "0%",
          containLabel: true,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "none",
          },
          formatter: function (params) {
            return (
              params[0].name +
              "<br/>" +
              "<span style='display:inline-block;margin-right:5px;border-radius:10px;width:9px;height:9px;background-color:rgba(36,207,233,0.9)'></span>" +
              params[0].seriesName +
              " : " +
              Number(params[0].value.toFixed(2)).toLocaleString()
            );
          },
        },

        xAxis: {
          show: false,
          type: "value",
        },
        yAxis: [
          {
            type: "category",
            inverse: true,
            axisLabel: {
              show: true,
              inside: true,
              // align: "left",
              padding: paddingY,
              color: "#fff",
              fontSize: fontSize,
            },
            splitLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false,
            },
            data: this.leftTitleData,
          },
          {
            type: "category",
            inverse: true,
            axisTick: "none",
            axisLine: "none",
            show: true,
            axisLabel: {
              textStyle: {
                show: true,
                inside: true,
                 align: 'right',
                padding: paddingX,
                color: "#fff",
                fontSize: fontSize,
              },
              formatter: function (value) {
                return value.toLocaleString() + nba;
              },
              // formatter: [
              //   '{a|{value}}'
              // ].join('\n'),
              rich: {
                a: {
                  color: "#fff",
                  fontSize: fontSize,
                  padding: [0, 0, 0, 0],
                },
                b: {
                  color: "#fff",
                  fontSize: fontSize,
                  // padding: [-15, 70, 15, 70],
                },
              },
            },
            data: infoData,
          },
        ],
        series: [
          {
            name: "个数",
            type: "bar",
            zlevel: 1,
            itemStyle: {
              normal: {
                barBorderRadius: nowSize(30),
                color: "rgba(1, 238, 246, 1)",
              },
            },
            barWidth: barWidth,
            data: infoData,
          },
          {
            name: "背景",
            type: "bar",
            barWidth: barWidth,
            barGap: "-100%",
            data: [300, 300, 300, 300, 300, 300],
            itemStyle: {
              normal: {
                color: "rgba(255, 255, 255, 0.15)",
                barBorderRadius: nowSize(30),
              },
            },
          },
        ],
      });
    },
  },
};
</script>
<style lang="scss" scoped>
</style>
