<template>
  <div class="chart-line">
    <div :id="id" class="pie" style="width: 100%; height: 100%;"></div>
  </div>
</template>
<script>
export default {
  props: {
    id: {
      // 图唯一标识
      default: function () {
        return "";
      }
    },
    chartType: {
      default: "line"
    },
    barWidth: {
      type: Number,
      default: 16
    },
    fontSize: {
      type: Number,
      default: 28
    },
    xAxis: {
      // x轴数据
      type: Array,
      default: function () {
        return [];
      }
    },
    yAxis: {
      type: Array,
      default: function () {
        // return [1600, 2300, 1200, 1600, 500, 1200, 1600, 200, 1200];
        return [];
      }
    },
    yMaxData: {
      type: Number,
      default: function () {
        return 0;
      }
    },
    // 柱状图 颜色 
    colorArrs: {
      type: Array,
      default: function () {
        return  ["#32F1FE", "rgba(49, 238, 250, 0.20)"] 
      }
    },
    // y轴显示类型，百分比，数值
    yType: {
      type: String,
      default: function () {
        return 'number'       //  百分比 ratio
      }
    },
    // y轴显示为数值的时候，最大值需要传入（考虑不同需求）
    yMaxNumber: {
      type: String,
      default: function () {
        return '1000'
      }
    },
    // y轴上面的数值是否显示
    showLaber: {
      type: Boolean,
      default: function () {
        return true
      }
    }
  },
  data: function () {
    return {
      echartsOption: null,
      screenWidth: document.body.clientWidth
    };
  },
  computed: {},
  watch: {
    yAxis: {
      deep: true,
      handler: function () {
        this.initChart();
      }
    },
    screenWidth(val) {
      this.screenWidth = val;
      this.echartsOption.resize()
    }
  },
  activated() { },
  mounted() {
    const self = this
    // 基于准备好的dom，初始化echarts实例
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        self.screenWidth = window.screenWidth
      })()
    }
    this.initChart();
  },
  methods: {
    newline(option, number, axis) {
      /* 此处注意你的json是数组还是对象 */
      option[axis][0]["axisLabel"] = {
        interval: 0,
        fontSize: this.fontSize,
        color: "#fff",
        // 计算底部字数
        formatter: function (params) {
          var newParamsName = "";
          var paramsNameNumber = params.length;
          var provideNumber = number;
          var rowNumber = Math.ceil(paramsNameNumber / provideNumber);
          if (paramsNameNumber > provideNumber) {
            for (var p = 0; p < rowNumber; p++) {
              var tempStr = "";
              var start = p * provideNumber;
              var end = start + provideNumber;
              if (p == rowNumber - 1) {
                tempStr = params.substring(start, paramsNameNumber);
              } else {
                tempStr = params.substring(start, end) + "\n";
              }
              newParamsName += tempStr;
            }
          } else {
            newParamsName = params;
          }
          return newParamsName;
        }
      };
      return option;
    },
    // 计算总数，输出百分比数组
    computeRatio() {
      let sum = 0
      let vm = this.yAxis
      let target = []
      sum = vm.reduce((prev, curr) => {
        return prev + curr
      })
      for(const i in this.yAxis) {
        let _vm = this.yAxis[i]
        target.push(
          ((_vm/sum)*100).toFixed(0)
        )
      }
      return target
    },
    initChart() {
      // y 轴 百分比还是 数值
      let yTypeShow = ''        // '{value}%' y轴显示方式
      let yMax = ''             // y轴最大值
      let yAxisData = []  // 柱状数值
      if (this.yType == 'number') {
        yTypeShow = '{value}'
        // 如果为数值，需要传入一个最大y值
        yMax = this.yMaxNumber
        yAxisData = this.yAxis  // y轴显示数值
      } else if (this.yType == 'ratio') {
        yTypeShow = '{value}%'        // 显示的值 value 最大为 100
        yMax = '100'
        yAxisData = this.yAxis  // y轴显示数值
        // let showYdata = this.computeRatio()
        // console.log("baifenbi", showYdata)
        // yAxisData = showYdata 
      }
      //当前视口宽度
      let nowClientWidth = document.documentElement.clientWidth;
      // 换算方法
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };
      let colors = ["#3979E9", "#fff", "#01EEF6", "#2D99FF", "#1B3C96"];
      this.echartsOption = this.$echarts.init(
        document.getElementById(this.id)
      );
      this.echartsOption.clear();
      // 绑定this
      let that = this;
      let option = {
        grid: {
          top: "12%",
          bottom: "15%",
          left: "10%",
          right: "10%"
        },
        xAxis: [
          {
            type: "category",
            data: this.xAxis,
            // max: 7,
            axisLine: {
              lineStyle: {
                color: colors[4],   // x 轴最底下的线
                width: nowSize(1),
              }
            },
            // 刻度
            // axisLabel: {
            //   show: true,
            //   interval: 0, // 解决x轴文字太长显示不出来的问题
            //   textStyle: {
            //     color: colors[4],
            //     fontWeight: 600,
            //     fontSize: nowSize(this.fontSize)
            //   }
            // },
            axisLabel: {  
              interval: 0,  
              margin: nowSize(10),
              textStyle: {
                color: '#fff',
                fontWeight: 600,
                fontSize: nowSize(14)
              },
              formatter:function(value)  
              {   
                  var ret = "";//拼接加\n返回的类目项  
                  var maxLength = 5;//每项显示文字个数  
                  var valLength = value.length;//X轴类目项的文字个数  
                  var rowN = Math.ceil(valLength / maxLength); //类目项需要换行的行数  
                  if (rowN > 1)//如果类目项的文字大于3,  
                  {  
                      for (var i = 0; i < rowN; i++) {  
                          var temp = "";//每次截取的字符串  
                          var start = i * maxLength;//开始截取的位置  
                          var end = start + maxLength;//结束截取的位置  
                          //这里也可以加一个是否是最后一行的判断，但是不加也没有影响，那就不加吧  
                          temp = value.substring(start, end) + "\n";  
                          ret += temp; //凭借最终的字符串  
                      }  
                      return ret;  
                  }  
                  else {  
                      return value;  
                  }  
              }  
            },
            axisTick: {
              show: true,
              inside: true,
              length: nowSize(5)
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            scale: true,
            min: 0,
            max: yMax,
            splitNumber: 5,
            axisLine: {
              show: false,
              lineStyle: {
                color: colors[2],
                width: nowSize(5)
              }
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: true,
              margin: nowSize(30),
              formatter: yTypeShow,
              textStyle: {
                // y轴左边边部分的文字
                color: "#ffffff",
                fontSize: nowSize(this.fontSize),
                fontWeight: 600, 
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: colors[4]
              }
            }
          }
        ],
        series: [
          {
            type: this.chartType,
            barWidth: nowSize(this.barWidth),
            // itemStyle: {
            //   color: colors[2]
            // },
            itemStyle: {
              normal: {
                color: function (params) {
                  let colorArr = that.colorArrs;
                  return new that.$echarts.graphic.LinearGradient(
                    0,
                    0,
                    0,
                    1,
                    [
                      {
                        offset: 0,
                        color: colorArr[0] // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: colorArr[1] // 100% 处的颜色
                      }
                    ],
                    false
                  );
                }
                // barBorderRadius: [30,30,0,0]
              }
            },
            lineStyle: {
              color: colors[2],
              width: nowSize(5)
            },
            label: {
              normal: {
                show: this.showLaber,
                position: "top",
                color: "#00FFFF",
                fontSize: nowSize(14),
                // fontWeight: 600
              }
            },
            symbolSize: nowSize(20),
            data: yAxisData
          }
        ]
      }; 
      // option = this.newline(option, 5, "xAxis");
      this.echartsOption.setOption(option);
    }
  }
};
</script>
<style lang="scss">
.chart-line {
  position: relative;
  width: 100%;
  height: 100%;
  .pie {
    width: 100%;
    height: 100%;
  }
}
</style>
