<template>
  <div class="h-container">
    <div class="left">
      <div class="item">
        <Frame title="总览概况">
          <TotalData :totalData="totalData"></TotalData>
        </Frame>
      </div>
      <div class="item">
        <Frame title="总投入与产出情况">
          <div class="dropDown">
            <DropDown
              :key="'touru'"
              :initContent="years[0]"
              :subject="years"
              @changeSelect="changeYear"
            ></DropDown>
          </div>
          <ChartDoubleBar
            :id="'zongtouruchanchu'"
            :isDoubleLine="true"
            :xAxis="chartBarXAxisData"
            :yAxis="chartBarYAxisData"
          ></ChartDoubleBar>
        </Frame>
      </div>
    </div>
    <div class="middle">
      <Frame title="各街道产业情况">
        <Map></Map>
      </Frame>
    </div>
    <div class="right">
      <div class="item">
        <Frame title="各产业GDP贡献率">
          <div class="dropDown">
            <DropDown
              key="gdp"
              :initContent="years[0]"
              :subject="years"
              @changeSelect="changeYear"
            ></DropDown>
          </div>
          <ChartRoseView
            :id="'各产业GDP贡献率'"
            :xAxis="roseXData"
            :yAxis="roseYData"
          ></ChartRoseView>
        </Frame>
      </div>
      <div class="item">
        <Frame title="平台数量增长情况">
          <div class="dropDown">
            <DropDown
              key="pingtai"
              :id="'pingtai'"
              :initContent="years[0]"
              :subject="years"
              @changeSelect="changeYear"
            ></DropDown>
          </div>
          <ChartDoubleLine
            :id="'平台数量增长情况'"
            :yAxis="pingtaiData"
          ></ChartDoubleLine>
        </Frame>
      </div>
    </div>
  </div>
</template>

<script>
import Frame from "@/components/frame.vue";
import TotalData from "@/components/graph/totalData";
import ChartDoubleLine from "@/components/graph/chartDoubleLine";
import ChartRoseView from "@/components/graph/chartRoseView";
import ChartDoubleBar from "@/components/graph/chartDoubleBar.vue";
import Map from "@/components/map";
import DropDown from "@/components/dropDown";
export default {
  name: "home",
  components: {
    Frame,
    TotalData,
    Map,
    ChartDoubleLine,
    ChartRoseView,
    ChartDoubleBar,
    DropDown,
  },
  data() {
    return {
      totalData: {},
      pingtaiData: [
        [123, 234, 231, 23, 61, 126, 124, 581, 125, 235, 190, 238],
        [323, 434, 551, 623, 761, 826, 924, 1081, 1125, 1235, 1390, 1438],
      ],
      roseXData: [
        "招商引资",
        "总部经济及上市企业",
        "先进制造业",
        "现代服务业",
        "金融业",
        "科技创新",
        "产业文化",
        "建筑装饰设计行业",
        "专业行业协会",
      ],
      roseYData: [100, 95, 90, 80, 75, 70, 65, 60, 55],
      // roseYData: [],

      chartBarXAxisData: ["第一季度", "第二季度", "第三季度", "第四季度"],
      chartBarYAxisData: [
        [100, 95, 120, 180],
        [106, 105, 130, 160],
        [7, 9, 12, 5],
      ],
      years: [
        {
          value: "2020",
          text: "2020年",
        },
        {
          value: "2019",
          text: "2019年",
        },
        {
          value: "2018",
          text: "2018年",
        },
        {
          value: "2017",
          text: "2017年",
        },
      ],
    };
  },
  mounted() {
    // this.loadData();
  },
  methods: {
    loadData() {
      this.$httpApi.get("/dev-api/webstatisticapi/index", {}).then((res) => {
        console.log("webstatisticapi:", res);
        if (res.code == 200) {
        
          // 总览概况
          this.totalData = res.data.generalSituation;
          // 总投入产出情况
          let inoutArr = res.data.inputMoneyOutput;
          let yTotalMoney = [];
          let yTotalProduction = [];
          let yTotalGdp = [];
          for (let i = 0; i < inoutArr.length; i++) {
            yTotalMoney.push(inoutArr[i].totalMoney);
            yTotalProduction.push(inoutArr[i].totalProduction);
            yTotalGdp.push(inoutArr[i].totalGdp);
          }
          this.chartBarYAxisData = [yTotalMoney, yTotalProduction, yTotalGdp];

          // 产业GDP
          let industryList = res.data.industryPercent;
          let roseXData = [];
          let roseYData = [];
          for (let i = 0; i < industryList.length; i++) {
            roseXData.push(industryList[i].name);
            roseYData.push(industryList[i].percent);
          }
          this.roseXData = roseXData;
          this.roseYData = roseYData;

          // 平台统计情况
          let platforms = res.data.platformsGrowth;
          let platformsCompanys = [];
          let platformsProjects = [];
          for (let i = 0; i < platforms.length; i++) {
            platformsCompanys.push(platforms[i].enterprise);
            platformsProjects.push(platforms[i].money);
          }
          this.pingtaiData = [platformsCompanys, platformsProjects];         
        }
      });
    },
    changeYear(e) {
      console.log("选择的nian份:", e);
      if (e.id == "touru") {
      } else if (e.id == "gdp") {
      } else if ((e.id = "pingtai")) {
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.h-container {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;

  .left,
  .right {
    // background-color: #ff0000;
    width: 31%;
    height: 100%;
    padding: 0 8px;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    .item {
      width: 98%;
      height: 48%;
      position: relative;
    }
    .dropDown {
      position: absolute;
      top: 28px;
      right: 22px;
    }
  }

  .middle {
    flex: 1;
    height: 100%;
    padding: 11px 0;
  }
}
</style>