<template>
  <div
    :id="id"
    class="pie"
    :optionObj="echartData"
    style="width: 100%; height: 100%;"
  ></div>
</template>
<script>
export default {
  props: {
    id: {
      // type: Number,
      default: function () {
        return "";
      }
    },
    echartData: {
      type: Array,
      default: function () {
        return [];
      }
    },
  },
  data: function () {
    return {
      echartsOption: null,
      screenWidth: document.body.clientWidth
    };
  },
  computed: {},
  watch: {
    echartData: {
      // deep: true,
      handler: function () {
        this.setChart();
      }
    },
    screenWidth(val) {
      this.screenWidth = val;
      this.echartsOption.resize()
    }
  },
  activated() { },
  mounted() {
    const self = this
    // 基于准备好的dom，初始化echarts实例
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        self.screenWidth = window.screenWidth
      })()
    }
    this.setChart();
  },
  methods: {
    setChart() {
      this.setRoseChart();
    },
    setRoseChart() {
      //当前视口宽度
      let nowClientWidth = document.documentElement.clientWidth;
      // 换算方法
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };
      let data = this.echartData;
      this.echartsOption = this.$echarts.init(document.getElementById(this.id));
      this.echartsOption.clear();
      this.echartsOption.setOption({
        color: [
          "#FF5858",
          "#383AFF",
          "#5976FF",
          "#5BBAFF",
          "#00FFFF",
          "#20D666",
          "#70F785",
          "#FF8D45",
          "#FDF30D",
        ],
        tooltip: {
          trigger: 'item',
          formatter: function (param) { return param.name + '：' + (param.value).toFixed(2) + ' ' },
          // formatter: function (param) { return param.name + '：' + (param.value).toFixed(2) + ' ' + '(' + (((param.value) / sum) * 100).toFixed(0) + '%' + ')' },
        },
        calculable: true,
        series: [
          {
            name: '',
            type: 'pie',
            radius: ["40%", "55%"],
            center: ['50%', '45%'],
            // roseType: "radius", // 是否开启玫瑰图
            data: this.echartData,
            startAngle: 160,
            //防止标签重叠
            // avoidLabelOverlap: true,
            // left: nowSize(20),
            // label 标签 设置
            label: {
              // （格式化程序）
              formatter: "{b}\n{d}%",
              fontStyle: "normal",
              color: "#fff",
              fontSize: nowSize(14),
              position: "outside",
            },
            labelLine: {
              show: true,
              length: '4%',
              length2: '4%',
              lineStyle: {
                width: nowSize(1)
              }
            },
            // 加载特效
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function (idx) {
              return Math.random() * 200;
            }
          }
        ]
      });
    },
  }
};
</script>
<style lang="scss" scoped>
</style>
