export default function AMapLoader() {
    return new Promise((resolve, reject) => {
        console.log("amap:",window.AMap);
        if (window.AMap) {
            console.log("amap is not null")
            resolve(window.AMap);
        } else {
            console.log("amap is null")
            window.onLoad  = function(){
                console.log("map init finish")
                resolve(window.AMap);
            }
            console.log("amap:",window);
            var url = 'https://webapi.amap.com/maps?v=1.4.15&key=b421ddff8a07a8122ce81c09fece3aa3&callback=onLoad';
            var jsapi = document.createElement('script');
            jsapi.charset = 'utf-8';
            jsapi.src = url;
            jsapi.onerror = reject
            document.head.appendChild(jsapi);
            
        }

    })
}