<template>
  <div class="chart-bar">
    <div class="b-titles">
      <div class="b-item" v-for="(item, i) in titleArr" :key="i">
        <span>{{ item.title }}</span>
        <span class="b-num" :style="{ color: item.numColor }">{{
          item.num
        }}</span>
        <span class="b-num1" :style="{ color: item.numColor }">{{
          item.unit
        }}</span>
      </div>
    </div>
    <div :id="id" class="pie" style="width: 100%; height: 100%"></div>
  </div>
</template>
<script>
export default {
  props: {
    id: {
      // 柱状图唯一标识
      default: function () {
        return "";
      },
    },
    barWidth: {
      type: Number,
      default: 15,
    },
    fontSize: {
      type: Number,
      default: 13,
    },
    isDoubleLine: {
      type: Boolean,
      default: false,
    },
    isSingleBar: {
      //设置为true只显示单条柱状图，并且只显示yAxis的第一个数组的值，传值方式不变
      type: Boolean,
      default: false,
    },
    xAxis: {
      // x轴数据
      type: Array,
      default: function () {
        // return ["妇女", "儿童", "家庭"];
        return [];
      },
    },
    yAxis: {
      // y轴数据  [[数组1的数据], [数组2的数据]]
      type: Array,
      default: function () {
        // return [
        //   [1600, 2300, 1200], // 活动的数据
        //   [1500, 1000, 1700] // 服务的数据
        // ];
        return [];
      },
    },
    yMaxData: {
      // 可以不用传最大值 默认有最大值
      // y轴最大值  [最大值1, 最大值2]
      type: Array,
      default: function () {
        // return [2000, 5000];
        return [];
      },
    },
    titleArr: {
      type: Array,
      default: function () {
        // return [
        //   {
        //     title: "2020年展开活动",
        //     num: "4289",
        //     unit: "场",
        //     numColor: "#2d99ff"
        //   },
        //   { title: "服务", num: "530980", unit: "人次", numColor: "#01eef6" }
        // ];
        return [];
      },
    },
  },
  data: function () {
    return {
      echartsOption: null,
    };
  },
  computed: {},
  watch: {
    yAxis: {
      deep: true,
      handler: function () {
        this.initChart();
      },
    },
  },
  activated() {},
  mounted() {
    this.initChart();
  },
  methods: {
    /**
     * Created by HanSon on 2016/1/24.
     */
    newline(option, number, axis) {
      /* 此处注意你的json是数组还是对象 */
      option[axis][0]["axisLabel"] = {
        interval: 0,
        fontSize: this.fontSize,
        color: "#fff",
        formatter: function (params) {
          var newParamsName = "";
          var paramsNameNumber = params.length;
          var provideNumber = number;
          var rowNumber = Math.ceil(paramsNameNumber / provideNumber);
          if (paramsNameNumber > provideNumber) {
            for (var p = 0; p < rowNumber; p++) {
              var tempStr = "";
              var start = p * provideNumber;
              var end = start + provideNumber;
              if (p == rowNumber - 1) {
                tempStr = params.substring(start, paramsNameNumber);
              } else {
                tempStr = params.substring(start, end) + "\n";
              }
              newParamsName += tempStr;
            }
          } else {
            newParamsName = params;
          }
          return newParamsName;
        },
      };
      return option;
    },
    initChart() {
      //当前视口宽度
      let nowClientWidth = document.documentElement.clientWidth;
      // 换算方法
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };

      let xAxis = [];
      if (this.isDoubleLine) {
        for (const i in this.xAxis) {
          let element = this.xAxis[i];
          if (element.indexOf("(") != -1) {
            let pre = element.slice(0, element.indexOf("("));
            let last = element.substring(element.indexOf("("));
            let len = (8 - pre.length) / 2;
            for (let j = 0; j < len; j++) {
              pre = " " + pre + " ";
            }
            if (pre.length > 8) {
              pre = pre.slice(0, 8);
            }
            element = pre + last;
          }
          xAxis.push(element);
        }
      } else {
        xAxis = this.xAxis;
      }

      let colors = ["#3979E9", "#fff", "#01EEF6", "#FF7E7E", "#FF7E7E"];
      let lineColors = ["#00FFFF", "#FFDC19", "#FF6565"];
      const echartsOption = this.$echarts.init(
        document.getElementById(this.id)
      );
      echartsOption.clear();
      let option = {
        grid: {
          top: "13%",
          bottom: "10%",
          left: "10%",
          right: "13%",
        },
        legend: {
          show: true,
          color: lineColors,
          zlevel: 101,
          top: "1%",
          right: "8%",
          data: ["投入金额", "生产总值", "GDP增长率"],
          itemWidth: 10, //图例的宽度
          itemHeight: 10, //图例的高度
          textStyle: {
            color: "#fff",
            fontSize: nowSize(13),
          },
        },
        xAxis: [
          {
            type: "category",
            data: xAxis,
            axisLine: {
              lineStyle: {
                color: colors[2],
                width: nowSize(1),
              },
            },
            // 刻度
            axisLabel: {
              //  rotate:40,
              // interval: 0, // 解决x轴文字太长显示不出来的问题
              show: true,
              textStyle: {
                color: '#fff',
                fontWeight: 600,
                fontSize: nowSize(16)
              },
            },
            axisTick: {
              show: true,
              inside: true,
              length: nowSize(4),
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            scale: true,
            min: 0,
            splitNumber: 5,
            axisLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            axisLabel: {
              show: true,
              textStyle: {
                // y轴左边边部分的文字
                color: "#00FFFF",
                fontSize: nowSize(this.fontSize),
                fontWeight: 600,
              },
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: colors[0],
              },
            },
          },
          {
            type: "value",
            scale: true,
            min: 0,
            splitNumber: 5,
            boundaryGap: [0.2, 0.2],
            axisLine: {
              show: false,
              //   lineStyle: {
              //     color: colors[2],
              //     width: 5,
              //   }
            },
            axisLabel: {
              show: true,
              textStyle: {
                // y轴右边部分的文字
                color: "#FF6565",
                fontSize: nowSize(this.fontSize),
                fontWeight: 600,
              },
            },
            axisTick: {
              show: false,
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "投入金额",
            type: "bar",
            yAxisIndex: 0, //以左边的y轴为标准
            barWidth: nowSize(this.barWidth),
            itemStyle: {
              color: lineColors[0],
            },
            data: this.yAxis[0],
          },
          {
            name: "生产总值",
            type: "bar",
            yAxisIndex: 0,
            barWidth: nowSize(this.barWidth),
            itemStyle: {
              color: lineColors[1],
            },
            data: this.yAxis[1],
          },
          {
            name: "GDP增长率",
            type: "line",
            yAxisIndex: 1, //以右边的y轴为标准
            smooth: true,
            zlevel: 3,
            showAllSymbol: true,
            symbol: "circle",
            symbolSize: nowSize(9),
            lineStyle: {
              normal: {
                color: lineColors[2],
              },
            },
            itemStyle: {
              color: "#fff",
              borderColor: lineColors[2],
              borderWidth: nowSize(2),
            },
            data: this.yAxis[2],
          },
        ],
      };
      if (this.isDoubleLine) {
        option = this.newline(option, 8, "xAxis");
      }
      echartsOption.setOption(option);
    },
  },
};
</script>
<style lang="scss">
.pie {
  width: 100%;
  height: 100%;
}
.chart-bar {
  position: relative;
  width: 100%;
  height: 100%;
}
.b-titles {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  flex-direction: row;
  margin-top: 62px;
  margin-right: 50px;
  font-size: 30px;
  font-family: Microsoft YaHei;
  font-weight: 600;
  color: #ffffff;
  .b-item {
    margin-left: 26px;
    .b-num {
      font-size: 44px;
    }
    .b-num1 {
      color: #2d99ff;
    }
    .b-num2 {
      color: #01eef6;
    }
  }
}
</style>
