<template>
  <div class="sex-tatio-container">
    <div :id="id" class="SexRatio" style="width: 100%; height: 100%;"></div>
    <div class="sex-title">{{id}}</div>
  </div>
</template>
<script>
export default {
  props: {
    id: {
      type: String,
      default: function () {
        return '';
      }
    },
    echartData: {
      type: Array,
      default: function () {
        return [];
      }
    },
  },
  data: function () {
    return {
      echartsOption: null,
      screenWidth: document.body.clientWidth,
      manScale: 0,
      womanScale: 0,
    };
  },
  computed: {},
  watch: {
    echartData: {
      // deep: true,
      handler: function () {
        this.setChart();
      }
    },
    screenWidth(val) {
      this.screenWidth = val;
      this.echartsOption.resize()
    }
  },
  activated() { },
  mounted() {
    const self = this;
    // 基于准备好的dom，初始化echarts实例
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        self.screenWidth = window.screenWidth
      })()
    };
    this.setChart();
  },
  methods: {
    scale() {
      let vm = JSON.parse(JSON.stringify(this.echartData));
      // console.log("this.v我m", vm)
      let sum = 0
      for (let i = 0; i < vm.length; i++) {
        sum += vm[i].value
      }
      let manNum = vm[0].value;
      let womanNum = vm[1].value;
      let manNumScale = ((manNum / sum) * 100).toFixed(2);
      let womanNumScale = ((womanNum / sum) * 100).toFixed(2);
      this.manScale = manNumScale;
      this.womanScale = womanNumScale;
      // console.log("this.manScale", this.manScale)
      // console.log("this.womanScale", this.womanScale)
    },
    setChart() {
      this.scale();
      //当前视口宽度
      let nowClientWidth = document.documentElement.clientWidth;
      // 换算方法
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };
      // console.log("this.ec", this.echartData)
      let vm = JSON.parse(JSON.stringify(this.echartData))
      // console.log("vm", vm)
      let peopleNum = [vm[0].value, vm[1].value]

      let datas = [];
      let colors = ['#2467FF', '#76F87B', '#32F1FE', '#FDF30D', '#FF5D5D']
      for (let i = 0; i < vm.length; i++) {
        datas.push({
          value: vm[i].value,
          name: vm[i].name,
          itemStyle: {
            normal: {
              label: {
                show:true
              },
              labelLine: {
                show:true
              },
              borderWidth:nowSize(1),
              // shadowBlur: nowSize(10),
              borderColor: '#1948AD',
              shadowColor: colors[i]
            }
          }
        },
        // {
        //   value: nowSize(0),
        //   name: '',
        //   itemStyle: {
        //     normal: {
        //       label: {
        //         show: false
        //       },
        //       labelLine: {
        //         show: false
        //       },
        //       color: 'rgba(0, 0, 0, 0)',
        //       borderColor: 'rgba(0, 0, 0, 0)',
        //       borderWidth: 0
        //     }
        //   }
        // }
        );
      }
      this.echartsOption = this.$echarts.init(
        document.getElementById(this.id)
      );
      this.echartsOption.clear();
      this.echartsOption.setOption({
        color: colors,
        tooltip: {
          show: true
        },
        legend: {
          show: false,
          icon: "circle",
          bottom: "12%",
          orient: "vertical",
          width: "70%",
          align: "left",
          itemGap: nowSize(120),
          // padding: [5, 50, 500, 100],
          itemHeight: nowSize(16),
          formatter: function (params) {
            var legendIndex = 0;
            datas.forEach(function (v, i) {
              if (v.name == params) {
                legendIndex = i;
              }
            });
            return datas[legendIndex].value + "人";
          },
          textStyle: {
            color: "#fff",
            fontSize: nowSize(20),
            fontWeight: "bold",
            fontStyle: "normal",
            padding: [nowSize(4), 0, 0, nowSize(8)]
          }
        },
        toolbox: {
          show: false
        },
        series: [{

          name: '',
          type: 'pie',
          // label 标签 设置
          label: {
            // （格式化程序）
            formatter: "{b}\n{c}项",
            fontStyle: "normal",
            // color: "#fff",
            fontSize: nowSize(14),
            position: "outside",
          },
          labelLine: {
            show: true,
            length: '4%',
            length2: '4%',
            lineStyle: {
              width: nowSize(1)
            }
          },
          // 加载特效
          animationType: "scale",
          animationEasing: "elasticOut",
          animationDelay: function (idx) {
            return Math.random() * 200;
          },
          clockWise: true,
          startAngle: 90,
          radius: ['42%', '55%'],
          center: ['50%', '45%'],
          hoverAnimation: true,
          data: datas
        }, 
        // {
        //   name: 'neihuan',
        //   type: 'pie',
        //   label: {
        //     show: false
        //   },
        //   clockWise: false,
        //   radius: ['33%', '36%'],
        //   center: ['50%', '45%'],
        //   hoverAnimation: false,
        //   data: '100%',
        //   itemStyle: {
        //     color: '#10336E',
        //     opacity: 0.8,
        //   },
        // },
        ]
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.sex-tatio-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-content: center;
  position: relative;
  .SexRatio {
  }
  .sex-title {
    position: absolute;
    text-align: center;
    padding-bottom: 4vh;
    color: #fff;
    bottom: 0;
    left: 50%;
    transform: translate3d(-50%,0%,0);
    font-size: 18px;
  }
}
</style>
