<template>
  <div class="total">
    <div class="t-item">
      <div class="t-title">投入金额总数</div>
      <div>
        <span class="t-num">{{ dataArr[0] | formatter }}</span>
        <span class="t-unit">万元</span>
      </div>
    </div>
    <div class="t-item">
      <div class="t-title">政策总数</div>
      <div>
        <span class="t-num c-yellow">{{ dataArr[1] | formatter }}</span>
        <span class="t-unit">项</span>
      </div>
    </div>
    <div class="t-item">
      <div class="t-title">申报项目总数</div>
      <div>
        <span class="t-num c-pinkred">{{ dataArr[2] | formatter }}</span>
        <span class="t-unit">项</span>
      </div>
    </div>
    <div class="t-item">
      <div class="t-title">企业总量</div>
      <div>
        <span class="t-num c-pinkred">{{ dataArr[3] | formatter }}</span>
        <span class="t-unit">家</span>
      </div>
    </div>
    <div class="t-item">
      <div class="t-title">企业生产总值</div>
      <div>
        <span class="t-num">{{ dataArr[4] | formatter }}</span>
        <span class="t-unit">亿元</span>
      </div>
    </div>
    <div class="t-item">
      <div class="t-title">企业增长率</div>
      <div>
        <span class="t-num c-green">{{ dataArr[5] }}</span>
        <span class="t-unit">%</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "totalData",
  props: {
    totalData: {
      // 图唯一标识
      default: function () {
        return {};
      },
    },
  },
  watch: {
    totalData: {
      deep: true,
      handler: function () {
        this.initData();
      },
    },
  },
  data() {
    return {
      dataArr: [3898000, 8922, 5467372, 9893, 89, 15.4],
      // dataArr:[]
    };
  },
  methods: {
    initData() {
      let d = this.totalData;
      this.dataArr = [
        d.totalMoney,
        d.totalPolicy,
        d.totalProject,
        d.totalEnterprise,
        d.totalEnterpriseGdp,
        d.enterpriseGrowthRate,
      ];
    },
  },
  filters: {
    formatter(number) {
      if(!number) {
        return ""
      }
      const numbers = number.toString().split("").reverse();
      const segs = [];
      while (numbers.length) segs.push(numbers.splice(0, 3).join(""));
      return segs.join(",").split("").reverse().join("");
    },
  },
};
</script>

