<template>
  <div class="chart-line">
    <div :id="id" class="pie" style="width: 100%; height: 100%;"></div>
  </div>
</template>
<script>
export default {
  name: "chartLine",
  props: {
    id: {
      // 图唯一标识
      default: function () {
        return "";
      }
    },
    fontSize: {
      type: Number,
      default: 16
    },
    xAxis: {
      // x轴数据
      type: Array,
      default: function () {
        // return ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月"];
        return [];
      }
    },
    yAxis: {
      // y轴数据
      type: Array,
      default: function () {
        // return [1600, 2300, 1200, 1600, 500, 1200, 1600, 200, 1200];
        return [];
      }
    }
  },
  data: function () {
    return {
      echartsOption: null,
      screenWidth: document.body.clientWidth
    };
  },
  computed: {},
  watch: {
    yAxis: {
      deep: true,
      handler: function () {
        this.initChart();
      }
    },
    screenWidth(val) {
      this.screenWidth = val;
      this.echartsOption.resize()
    }
  },
  activated() { },
  mounted() {
    const self = this
    // 基于准备好的dom，初始化echarts实例
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        self.screenWidth = window.screenWidth
      })()
    }
    this.initChart();
  },
  methods: {
    initChart() {
      //当前视口宽度
      let nowClientWidth = document.documentElement.clientWidth;
      // 换算方法
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };
      // name 的 padding 计算
      let paddingName = [0, 0, 11, -70];
      paddingName = paddingName.map(element => {
        return nowSize(element)
      });
      console.log('name padding', paddingName)
      // let colors = ["#3979E9", "#fff", "#01EEF6", "#2D99FF"];
      this.echartsOption = this.$echarts.init(document.getElementById(this.id));
      this.echartsOption.clear();
      this.echartsOption.setOption({
        // backgroundColor: "#123B90",
        grid: {
          top: "12%",
          left: "12%",
          right: "6%",
          bottom: "15%"
          //   containLabel: true
        },
        xAxis: [
          {
            type: "category",
            axisLine: {
              lineStyle: {
                color: "#1B3C96",
                width: nowSize(1)
              }
            },
            axisTick: {
              show: true,
              inside: true,
              length: nowSize(5)
            },
            axisLabel: {  
              interval: 0,  
              margin: nowSize(10),
              textStyle: {
                color: '#fff',
                fontWeight: 600,
                fontSize: nowSize(14)
              },
              formatter:function(value)  
              {   
                  var ret = "";//拼接加\n返回的类目项  
                  var maxLength = 5;//每项显示文字个数  
                  var valLength = value.length;//X轴类目项的文字个数  
                  var rowN = Math.ceil(valLength / maxLength); //类目项需要换行的行数  
                  if (rowN > 1)//如果类目项的文字大于3,  
                  {  
                      for (var i = 0; i < rowN; i++) {  
                          var temp = "";//每次截取的字符串  
                          var start = i * maxLength;//开始截取的位置  
                          var end = start + maxLength;//结束截取的位置  
                          //这里也可以加一个是否是最后一行的判断，但是不加也没有影响，那就不加吧  
                          temp = value.substring(start, end) + "\n";  
                          ret += temp; //凭借最终的字符串  
                      }  
                      return ret;  
                  }  
                  else {  
                      return value;  
                  }  
              }  
            },
            splitLine: {
              show: false
            },
            boundaryGap: false,
            data: this.xAxis
          }
        ],
        yAxis: [
          {
            type: "value",
            min: 0,
            splitNumber: 5,
            name: "单位（家）企业",
            nameTextStyle: {
              color: "#FFF",
              fontStyle: "normal",
              // fontWeight: "bold",
              fontSize: nowSize(16),
              align: "center",
              verticalAlign: "bottom",
              lineHeight: nowSize(14),
              padding: paddingName
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "#00BFF3",
                opacity: 0.23
              }
            },
            axisLine: {
              show: false
            },
            axisLabel: {
              show: true,
              margin: nowSize(36),
              fontSize: nowSize(this.fontSize),
              fontWeight: 600,
              fontFamily: "Microsoft YaHei",
              textStyle: {
                color: "#fff"
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            name: "",
            type: "line",
            smooth: true,     // 开启光滑
            showAllSymbol: true,
            symbol: "circle",
            symbolSize: nowSize(8),
            lineStyle: {
              normal: {
                color: "#00FFFF"
              }
            },
            label: {
              show: true,
              position: "top",
              fontSize: nowSize(this.fontSize),
              // fontWeight: 600,
              fontFamily: "Microsoft YaHei",
              textStyle: {
                color: "#00FFFF"
              }
            },
            itemStyle: {
              color: "#fff",
              borderColor: "#00FFFF",
              borderWidth: nowSize(2)
            },
            areaStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "rgba(0, 255, 255, 0.52)"
                    },
                    {
                      offset: 1,
                      color: "rgba(0, 255, 255, 0.11)"
                    }
                  ],
                  false
                )
              }
            },
            data: this.yAxis
          }
        ]
      });
    }
  }
};
</script>
<style lang="scss">
.chart-line {
  position: relative;
  width: 100%;
  height: 100%;
  .pie {
    width: 100%;
    height: 100%;
  }
}
</style>
