<template>
  <div class="chart-line">
    <div :id="id" class="pie" style="width: 100%; height: 100%"></div>
  </div>
</template>
<script>
export default {
  name: "chartDoubleLine",
  props: {
    id: {
      // 图唯一标识
      default: function () {
        return "";
      },
    },
    fontSize: {
      type: Number,
      default: 13,
    },
    xAxis: {
      // x轴数据
      type: Array,
      default: function () {
        return [
          "1月",
          "2月",
          "3月",
          "4月",
          "5月",
          "6月",
          "7月",
          "8月",
          "9月",
          "10月",
          "11月",
          "12月",
        ];
        // return [];
      },
    },
    yAxis: {
      // y轴数据
      type: Array,
      default: function () {
        // return [数组1， 数组2]
        return [];
      },
    },
  },
  data: function () {
    return {
      echartsOption: null,
      screenWidth: document.body.clientWidth,
    };
  },
  computed: {},
  watch: {
    yAxis: {
      deep: true,
      handler: function () {
        this.initChart();
      },
    },
    screenWidth(val) {
      this.screenWidth = val;
      this.echartsOption.resize();
    },
  },
  activated() {},
  mounted() {
    const self = this;
    // 基于准备好的dom，初始化echarts实例
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth;
        self.screenWidth = window.screenWidth;
      })();
    };
    this.initChart();
  },
  methods: {
    initChart() {
      //当前视口宽度
      let nowClientWidth = document.documentElement.clientWidth;
      // 换算方法
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };
      this.echartsOption = this.$echarts.init(document.getElementById(this.id));
      this.echartsOption.clear();

      let colorArr = ["#FFCE00", "#00FFFF"];
      let option = {
        grid: {
          top: "12%",
          left: "15%",
          right: "4%",
          bottom: "15%",
        },
        legend: {
          show: true,
          color: colorArr,
          zlevel: 101,
          top: "1%",
          right: "5%",
          data: ["注册企业数", "累计项目数"],
          itemWidth: 15, //图例的宽度
          itemHeight: 10, //图例的高度
          textStyle: {
            color: "#fff",
          },
        },
        xAxis: [
          {
            type: "category",
            axisLine: {
              lineStyle: {
                color: "#01EEF6",
                width: nowSize(1),
              },
            },
            axisTick: {
              show: true,
              inside: true,
              length: nowSize(5),
            },

            axisLabel: {
              margin: nowSize(12),
              color: "#ffffff",
              fontSize: nowSize(this.fontSize),
              fontWeight: 600,
              fontFamily: "Microsoft YaHei",
            },
            splitLine: {
              show: false,
            },
            boundaryGap: false,
            data: this.xAxis,
          },
        ],
        yAxis: [
          {
            type: "value",
            min: 0,
            splitNumber: 6,
            splitLine: {
              show: true,
              lineStyle: {
                color: "#00BFF3",
                opacity: 0.23,
              },
            },
            axisLine: {
              show: false,
            },
            axisLabel: {
              show: true,
              margin: nowSize(36),
              fontSize: nowSize(this.fontSize),
              fontWeight: 600,
              fontFamily: "Microsoft YaHei",
              textStyle: {
                color: "#fff",
              },
            },
            axisTick: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "注册企业数",
            type: "line",
            smooth: true,
            zlevel: 3,
            showAllSymbol: true,
            symbol: "circle",
            symbolSize: nowSize(8),
            lineStyle: {
              normal: {
                color: "#31EDFE",
              },
            },
            itemStyle: {
              color: "#fff",
              borderColor: "#31EDFE",
              borderWidth: nowSize(2),
            },
            areaStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "rgba(0, 255, 255, 0.73)",
                    },
                    {
                      offset: 1,
                      color: "rgba(0, 255, 255, 0)",
                    },
                  ],
                  false
                ),
              },
            },
            data: this.yAxis[0],
          },
          {
            name: "累计项目数",
            type: "line",
            smooth: true,
            zlevel: 3,
            showAllSymbol: true,
            symbol: "circle",
            symbolSize: nowSize(8),
            lineStyle: {
              normal: {
                color: "#FFCE00",
              },
            },
            itemStyle: {
              color: "#fff",
              borderColor: "#FFCE00",
              borderWidth: nowSize(2),
            },
            areaStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "rgba(255, 206, 0, 0.73)",
                    },
                    {
                      offset: 1,
                      color: "rgba(255, 206, 0, 0)",
                    },
                  ],
                  false
                ),
              },
            },
            data: this.yAxis[1],
          },
        ],
      };
      this.echartsOption.setOption(option);
    },
  },
};
</script>
<style lang="scss">
.chart-line {
  position: relative;
  width: 100%;
  height: 100%;
  .pie {
    width: 100%;
    height: 100%;
  }
}
</style>
