<template>
  <div class="home-container">
    <div class="home-top">
      <div class="header-top">
        <div class="header-title">产业扶持资金综合服务平台</div>
      </div>
      <div class="header-tabs flex-row">
        <div
          v-for="(item, i) in tabs"
          :key="i"
          :class="['tab', i === currentTab ? 'tab-on' : '']"
          @click="changeTab(i)"
        >
          {{ item }}
        </div>
      </div>
    </div>
    <div class="home-content">
      <transition  name="fade">
        <tab-home v-if="currentTab === 0"></tab-home>
        <tab-funds v-else-if="currentTab === 1"></tab-funds>
        <tab-industry v-else-if="currentTab === 2"></tab-industry>
        <tab-platform v-else></tab-platform>
      </transition>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import tabHome from "./tabHome/index";
import tabFunds from "./tabFunds/index";
import tabIndustry from "./tabIndustry/index";
import tabPlatform from "./tabPlatform/index";

export default {
  name: "Layout",
  computed: {
    ...mapGetters(["currentTab"]),
  },
  components: {
    tabHome,
    tabFunds,
    tabIndustry,
    tabPlatform,
  },
  data() {
    return {
      tabs: ["首页", "资金分析", "产业分析", "平台分析"],
    };
  },
  methods: {
    // loadData() {
    //   this.$httpApi.post("/zhfl/dptj/fezd/getFwdx", {}).then(res => {
    //     console.log("服务对象:", res);
    //   });
    // },
    changeTab(i) {
      if (this.currentTab != i) {
        this.$store.commit("updateCurrentTab", i);
        console.log("切换", i);
      }
    },
    swiperleft: function () {
      if (this.currentTab !== 3) {
        this.$store.commit("updateCurrentTab", this.currentTab + 1);
      } else {
        this.$store.commit("updateCurrentTab", 0);
      }
    },
    swiperright: function () {
      if (this.currentTab !== 0) {
        this.$store.commit("updateCurrentTab", this.currentTab - 1);
      } else {
        this.$store.commit("updateCurrentTab", 3);
      }
    },
  },
};
</script>

