<template>
  <div class="chart-line">
    <div :id="id" class="pie" style="width: 100%; height: 100%;"></div>
  </div>
</template>
<script>
export default {
  name: "chartLine",
  props: {
    id: {
      // 图唯一标识
      default: function () {
        return "";
      }
    },
    fontSize: {
      type: Number,
      default: 16
    },
    xAxis: {
      // x轴数据
      type: Array,
      default: function () {
        // return ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月"];
        return [];
      }
    },
    yAxis: {
      // y轴数据
      type: Array,
      default: function () {
        // return [1600, 2300, 1200, 1600, 500, 1200, 1600, 200, 1200];
        return [];
      }
    }
  },
  data: function () {
    return {
      echartsOption: null,
      screenWidth: document.body.clientWidth
    };
  },
  computed: {},
  watch: {
    yAxis: {
      deep: true,
      handler: function () {
        this.initChart();
      }
    },
    screenWidth(val) {
      this.screenWidth = val;
      this.echartsOption.resize()
    }
  },
  activated() { },
  mounted() {
    const self = this
    // 基于准备好的dom，初始化echarts实例
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        self.screenWidth = window.screenWidth
      })()
    }
    this.initChart();
  },
  methods: {
    initChart() {
      //当前视口宽度
      let nowClientWidth = document.documentElement.clientWidth;
      // 换算方法
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };
      let colors = ["#3979E9", "#fff", "#01EEF6", "#2D99FF"];
      this.echartsOption = this.$echarts.init(document.getElementById(this.id));
      this.echartsOption.clear();
      this.echartsOption.setOption({
        // backgroundColor: "#123B90",
        grid: {
          top: "12%",
          left: "10%",
          right: "10%",
          bottom: "20%"
          //   containLabel: true
        },
        xAxis: [
          {
            type: "category",
            axisLine: {
              lineStyle: {
                color: "#01EEF6",
                width: nowSize(1)
              }
            },
            axisTick: {
              show: true,
              inside: true,
              length: nowSize(5)
            },

            axisLabel: {
              margin: nowSize(30),
              color: "#ffffff",
              fontSize: nowSize(this.fontSize),
              fontWeight: 600,
              fontFamily: "Microsoft YaHei"
            },
            splitLine: {
              show: false
            },
            boundaryGap: false,
            data: this.xAxis
          }
        ],
        yAxis: [
          {
            type: "value",
            min: 0,
            splitNumber: 6,
            splitLine: {
              show: true,
              lineStyle: {
                color: "#00BFF3",
                opacity: 0.23
              }
            },
            axisLine: {
              show: false
            },
            axisLabel: {
              show: true,
              margin: nowSize(36),
              fontSize: nowSize(this.fontSize),
              fontWeight: 600,
              fontFamily: "Microsoft YaHei",
              textStyle: {
                color: "#fff"
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            name: "",
            type: "line",
            showAllSymbol: true,
            symbol: "circle",
            symbolSize: nowSize(8),
            lineStyle: {
              normal: {
                color: "#00FF9D"
              }
            },
            label: {
              show: true,
              position: "top",
              fontSize: nowSize(this.fontSize),
              fontWeight: 600,
              fontFamily: "Microsoft YaHei",
              textStyle: {
                color: "#00FF9D"
              }
            },
            itemStyle: {
              color: "#fff",
              borderColor: "#00FF9D",
              borderWidth: nowSize(2)
            },
            areaStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "rgba(0, 255, 157, 0.52)"
                    },
                    {
                      offset: 1,
                      color: "rgba(0, 255, 157, 0.11)"
                    }
                  ],
                  false
                )
              }
            },
            data: this.yAxis
          }
        ]
      });
    }
  }
};
</script>
<style lang="scss">
.chart-line {
  position: relative;
  width: 100%;
  height: 100%;
  .pie {
    width: 100%;
    height: 100%;
  }
}
</style>
